@import '../../../../../styles/templarios.abstracts';

.tp-news-search {
  &__container {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 0 24px;

    @include breakpoint-down('md') {
      flex-direction: column;
    }
  }

  &__term-search,
  &__category-filter {
    flex: 1 1 auto;
    width: 100%;
  }
}
